/// HEADER
.header__logo {
  @include mq($until: mobile) {
    text-align: center; }
  img {
    @include mq($until: mobile) {
      width: 260px; } } }

.header__slogan {
  font-size: 36px;
  line-height: 1.1em;
  padding-top: 7px;
  @include mq($until: desktop) {
    font-size: 28px; }
  span {
    display: block;
    font-size: 18px;
    font-weight: 300;
    margin-top: -12px;
    @include mq($until: desktop) {
      font-size: 15px;
      line-height: 1.2em;
      padding-top: 10px; } } }

.header__brief {
  position: relative;
  display: inline-block;
  @include mq($until: mobile) {
    margin-top: -10px; }
  .ico {
    position: absolute;
    left: 0;
    top: 0;
    @include mq($until: mobile) {
      transform: scale(.65);
      margin-left: -10px;
      margin-top: -8px; } }
  span {
    padding-left: 70px;
    display: block;
    font-weight: 300;
    line-height: 1.2em;
    @include mq($until: mobile) {
      padding-left: 50px; } }
  &:hover {
    opacity: .8; } }

.header__brief-name {
  font-size: 24px;
  padding-top: 3px;
  @include mq($until: mobile) {
    font-size: 18px; } }

.header__brief-action {
  font-size: 16px;
  padding-top: 5px;
  @include mq($until: mobile) {
    font-size: 12px;
    padding-top: 0; } }


//hero
.hero__navi {
  font-weight: 300;
  font-size: 16px;
  text-transform: uppercase;
  @include mq($until: bp1030) {
    font-size: 14px; }
  a {
    padding: 10px 14px 10px 0;
    transition: all .4s ease;
    display: inline-block;
    @include mq($until: tablet) {
      padding-right: 7px; } } }


.hero__navi-list {
  margin: 0;
  list-style: none;
  li {
    display: inline-block;
    position: relative;
    transition: all .4s ease;
    &:hover {
      a {
        opacity: .8; }
      .hero__navi-list__sublevel {
        display: block; } } } }

.hero__navi-list__sublevel {
  display: none;
  list-style: none;
  font-weight: 300;
  position: absolute;
  z-index: 10;
  top: 45px;
  left: -90px;
  width: 310px;
  background: white;
  box-shadow: 0px 5px 13px rgba(black, .3);
  border-radius: 6px;
  color: $fontColor;
  padding: 25px 35px;
  text-transform: none;
  li {
    display: inline-block;
    a {
      display: inline-block;
      padding: 3px 0;
      transition: all.4s ease;
      &:hover {
        color: $primary; } } }

  .triangle-with-shadow {
  	position: absolute;
  	top: -20px;
  	z-index: 2;
  	left: 70px;
  	.ico-triangle {
  		position: relative;
  		overflow: hidden;
  		display: block;
  		width: 80px;
  		height: 20px;
  		&:after {
  			content: ' ';
  			display: inline-block;
  			width: 40px;
  			height: 40px;
  			background: white;
  			transform: rotate(45deg) {
        border-radius: 4px; }
  			position: absolute;
  			top: 12px;
  			right: 5px;
  			box-shadow: -1px -1px 10px -2px rgba(black, 0.3); } } } }


.hero__phone {
  font-weight: 300;
  margin-top: -3px;
  @include mq($until: desktop) {
    margin-top: 20px; } }

.hero__phone-wrapper {
  padding-left: 10px;
  display: inline-block;
  @include mq($until: bp1030) {
    padding-left: 5px; } }

.hero__phone-number {
  font-size: 24px;
  display: inline-block;
  line-height: 1em;
  @include mq($until: bp1030) {
    font-size: 20px; } }

.hero__phone-callback {
  display: block;
  font-size: 14px;
  text-decoration: underline;
  line-height: 1.1em;
  @include mq($until: bp1030) {
    font-size: 12px; } }

.hero__title {
  margin-top: 10vh; }
.hero__title__main {
  font-size: 53px;
  font-weight: 600;
  line-height: 1em;
  text-align: center;
  @include mq($until: desktop) {
    font-size: 46px; }
  @include mq($until: tablet) {
    font-size: 40px; }
  @include mq($until: mobile) {
    font-size: 36px; }
  span {
    font-weight: 300;
    font-size: 45px;
    letter-spacing: 7px;
    display: block;
    @include mq($until: desktop) {
      font-size: 40px; }
    @include mq($until: tablet) {
      font-size: 36px;
      letter-spacing: 5px; }
    @include mq($until: mobile) {
      font-size: 31px;
      letter-spacing: 3px; } } }


.hero__form {
  font-size: 16px;
  margin: 0 auto;
  display: inline-block;
  margin-top: 40px;
  text-align: left;
  span {
    margin-bottom: 5px;
    display: inline-block;
    @include mq($until: desktop) {
      font-size: 14px; } }
  .form-input {
    @include mq($until: bp1080) {
      display: block;
      width: 100%; } }
  button {
    @include mq($until: bp1080) {
      display: block;
      width: 100%; } } }

.form-input {
  background: white;
  border: 1px solid $borderColor;
  padding: 10px 15px;
  margin-right: 15px;
  border-radius: 4px;
  display: inline-block;
  margin-bottom: 10px;
  &--block {
    display: block;
    width: 100%; } }

.hero__icons {
  text-align: center;
  margin-top: 10vh;
  .ico {
    @include mq($until: desktop) {
      transform: scale(.8);
      margin-bottom: -10px; }
    @include mq($until: mobile) {
      transform: scale(.65);
      margin-left: -15px;
      margin-bottom: -15px; } }
  .ico--hero-2 {
    @include mq($until: mobile) {
      margin-left: -25px; } }
  span {
    font-size: 18px;
    font-weight: 300;
    display: block;
    @include mq($until: desktop) {
      font-size: 16px; }

    @include mq($until: mobile) {
      margin-bottom: 15px;
      font-size: 14px; } } }


// services
.service__block {
  text-align: center;
  position: relative;
  padding: 20px 0;
  border-radius: 5px;
  @include mq($until: mobile) {
    padding: 10px 0;
    margin-top: -15px;
    min-height: 220px; }
  .ico {
    position: relative;
    @include mq($until: desktop) {
      transform: scale(.85);
      margin-bottom: -10px; }
    @include mq($until: tablet) {
      transform: scale(.7);
      margin-bottom: -15px; } }
  .ico--service-5 {
    @include mq($until: tablet) {
      margin-left: -40px;
      transform: scale(.6); } }
  span {
    font-size: 16px;
    font-weight: 300;
    display: block;
    margin-top: 5px;
    position: relative;
    z-index: 9;
    @include mq($until: tablet) {
      font-size: 14px; } }
  .service__block__desc {
    font-size: 16px;
    margin-top: 10px;
    text-align: left;
    line-height: 1.2em;
    font-weight: 300;
    @include mq($until: tablet) {
      font-size: 14px; } } }
.services--inner {
  .service__block {
    @include mq($until: mobile) {
      text-align: left;
      margin-bottom: -20px; }
    span {
      font-size: 24px;
      font-weight: 400;
      line-height: 1.2em;
      @include mq($until: desktop) {
        font-size: 20px; }
      @include mq($until: tablet) {
        font-size: 18px; }
      @include mq($until: mobile) {
        font-size: 24px; } }
    .ico {
      transition: all .4s ease;
      @include mq($until: mobile) {
        margin-left: -15px; } }
    &:hover {
      .ico {
        @include mq($until: hd) {
          transform: scale(1.1); }
        @include mq($until: desktop) {
          transform: scale(.95);
          margin-bottom: -10px; }
        @include mq($until: tablet) {
          transform: scale(.8);
          margin-bottom: -15px; }
        @include mq($until: mobile) {
          transform: scale(.7); } }

      .service__block__desc {
        @include mq($from: mobile) {
          opacity: 0; } } } }
  .service__block-hide {
    padding-top: 250px;
    padding-bottom: 40px;
    @include mq($until: mobile) {
      display: block;
      opacity: 1;
      position: relative;
      box-shadow: none;
      padding-top: 20px;
      left: 0;
      &::before {
        display: none; }
      .btn {
        display: inline-block !important;
        width: 47%;
        margin: 0;
        &.btn--outline {
          padding-top: 10px;
          padding-bottom: 10px; }
        &:first-child {
          margin-right: 3%; } } }
    &::before {
      height: 170px; } } }

.service__block-hide {
  opacity: 0;
  position: absolute;
  z-index: 2;
  top: 0;
  left: -15px;
  right: -15px;
  padding-top: 220px;
  transition: all .4s ease;
  box-shadow: 0px 0px 13px rgba(black, .3);
  @include mq($until: mobile) {
    padding-top: 200px;
    left: -10px;
    right: -10px; }
  .btn {
    @include mq($until: mobile) {
      font-size: 14px; } }
  &::before {
    display: block;
    content: ' ';
    background: white;
    position: absolute;
    bottom: 0;
    z-index: 1;
    height: 130px;
    width: 100%;
    @include mq($until: mobile) {
      height: 100px; } }
  &:hover {
    opacity: 1;
    z-index: 9; }
  .btn {
    margin-left: 20px;
    margin-right: 20px;
    position: relative;
    z-index: 2;
    width: auto; } }


$block1: #37a7e2;
$block2: #6fd87b;
$block3: #26b690;
$block4: #badf30;

//steps
.br-w {
  .steps__desc-heading {
    @include mq($until: desktop) {
        word-wrap: break-word;
        word-break: break-all; } } }
.steps__desc {
  position: relative;
  margin: 60px 0 60px 0;
  @include mq($until: mobile) {
    margin: 20px 0 20px 0; }
  &.first {
    margin-top: 27px;
    &::after {
      background: $block1; } }
  &.second {
    margin-top: 160px;
    @include mq($until: desktop) {
      margin-top: 130px; }
    &::after {
      background: $block2;
      left: -50%;
      width: 55%;
      top: 39px; } }
  &.third {
    margin-top: 135px;
    @include mq($until: desktop) {
      margin-top: 80px; }
    &::after {
      background: $block3; } }
  &.fourth {
    margin-top: 117px;
    @include mq($until: desktop) {
      margin-top: 80px; }
    .steps__desc-numbs {
      top: -10px; }
    &::after {
      background: $block4;
      left: -50%;
      width: 55%;
      top: 39px; } }
  &::after {
    display: block;
    position: absolute;
    left: 20px;
    top: 40px;
    width: 150%;
    height: 2px;
    content: ' ';
    z-index: 1;
    @include mq($until: wide) {
      display: none; } } }
.steps__desc-numbs {
  display: inline-block;
  position: absolute;
  top: -22px;
  left: 0;
  @include mq($until: mobile) {
    top: -18px;
    left: -10px; } }
.steps__desc__text {
  display: inline-block;
  padding-left: 45px;
  @include mq($until: mobile) {
    padding-left: 30px; } }

.steps__desc-heading {
  font-size: 30px;
  display: inline-block;
  word-wrap: break-word;
  @include mq($until: desktop) {
    font-size: 26px; }
  @include mq($until: tablet) {
    font-size: 24px; }
  @include mq($until: mobile) {
    font-size: 22px;
    word-break: break-all; } }

.steps__desc-desc {
  font-size: 16px;
  line-height: 1.2em;
  margin-top: 10px;
  @include mq($until: desktop) {
    font-size: 14px; } }
.ico-step-num {
  font-size: 72px;
  font-style: normal;
  display: inline-block;
  position: relative;
  line-height: 1em;
  @include mq($until: wide) {
    font-size: 65px; }

  @include mq($until: mobile) {
    font-size: 55px; } }

.ico-step-num-1 {
  color: $block1; }
.ico-step-num-2 {
  color: $block2; }
.ico-step-num-3 {
  color: $block3; }
.ico-step-num-4 {
  color: $block4; }

.steps__blocks {
  text-align: center; }

.steps__block {
  position: relative;
  display: inline-block;
  margin: -28px 0;
  &:nth-child(odd) {
    float: left;
    margin-left: 23px; }
  &:nth-child(even) {
    float: right;
    margin-right: 23px; }
  &:after {
    content: ' ';
    display: block;
    transform: rotate(-45deg);
    position: relative;
    z-index: 2;
    width: 190px;
    height: 190px; }
  .ico {
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 10; } }

@for $i from 1 through 44 {
  @media screen and (max-width: 1210px - ($i * 10)) {
    .steps__block {
      margin-top: -38px + ($i / 2);
      margin-bottom: -38px + ($i / 2); }
    .steps__block:after {
      width: 220px - $i;
      height: 220px - $i; }
    .steps__block:nth-child(odd) {
      margin-left: 100px - ($i * 1); }
    .steps__block:nth-child(even) {
      margin-right: 100px - ($i * 2.5); }
    .steps__blocks .ico {
      transform: scale(1 - ($i / 200) ) translate(-50%, -50%);
      margin-left: 0px - ($i / 3);
      margin-top: 0px - ($i / 3); } } }

.steps__block--1:after {
  background: $block1; }
.steps__block--2:after {
  background: $block2; }
.steps__block--3:after {
  background: $block3; }
.steps__block--4:after {
  background: $block4; }

.step-icons-mobile {
  img {
    margin-top: 20px; }
