// Portfolio
.portfolio__item {
  text-align: center;
  padding: 20px 0;
  @include mq($until: mobile) {
    padding: 20px 30px; }
  img {
    transition: all .4s ease; }
  &:hover {
    img {
      @include mq($from: hd) {
        transform: scale(1.2); }
      @include mq($until: wide) {
        transform: scale(1.1); }
      @include mq($until: mobile) {
        transform: scale(1); } } } }
.portfolio__item-hide {
  opacity: 0;
  position: absolute;
  z-index: 2;
  top: -20px;
  left: -15px;
  right: -15px;
  padding-top: 350px;
  padding-bottom: 50px;
  transition: all .4s ease;
  box-shadow: 0px 0px 13px rgba(black, .3);
  @include mq($from: hd) {
    left: -30px;
    right: -30px; }
  @include mq($until: wide) {
    padding-top: 300px; }
  @include mq($until: desktop) {
    padding-top: 250px; }
  @include mq($until: tablet) {
    padding-top: 200px; }
  @include mq($until: mobile) {
    left: 15px;
    right: 15px;
    padding-top: 280px; }
  &::before {
    display: block;
    content: ' ';
    background: white;
    position: absolute;
    bottom: 0;
    z-index: 1;
    height: 150px;
    width: 100%;
    @include mq($until: desktop) {
      height: 180px; }
    @include mq($until: mobile) {
      height: 150px; } }
  &:hover {
    opacity: 1;
    z-index: 9; }
  .btn {
    position: relative;
    display: inline-block;
    z-index: 2;
    width: auto;
    margin: 0;
    margin-bottom: 10px; } }

